.App {
  text-align: center;
  max-width: 600px;
  margin: 0 auto;
  padding: 0 20px; /* 기본 패딩 */
}

.image-container {
  margin: 3px 0;
  cursor: pointer;
  position: relative;
}

.text-overlay {
  background-color: #6c757d; /* 회색 배경 */
  color: white; /* 흰색 글자 */
  font-size: 20px;
  padding: 20px 0;
  text-align: center;
  width: 500px;
  padding: 10px 0;
  margin: 0 auto;
  position: relative;
  z-index: 1;
  transition: background-color 0.3s ease;
}

.text-overlay.active {
  background-color: #3f4245; /* 클릭 시 약간 어두운 색으로 변경 */
}

.image-style {
  width: 500px;
  height: auto;
  margin-top: 2px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid #ddd; /* 이미지 테두리 추가 */
  z-index: 0;
}

/* 반응형 디자인을 위한 미디어 쿼리 */
@media (max-width: 768px) {
  .text-overlay, .image-style {
    width: 100%; /* 전체 너비 사용 */
  }
  .App {
    padding: 0 10px; /* 작은 화면에서 패딩 조정 */
  }
}

@media (max-width: 480px) {
  .text-overlay, .image-style {
    width: 100%; /* 작은 화면에서도 전체 너비 사용 */
  }
  .App {
    padding: 0 5px; /* 더 작은 패딩 */
  }
  .text-overlay {
    font-size: 18px; /* 작은 화면에서 글자 크기 줄이기 */
    padding: 8px 0;
  }
}
